import React from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import ProductCartProductRatingStatisticComponent from "eshop/modules/simfree/components/rating/ProductCartProductRatingStatisticComponent";
import ProductCartProductRecensionComponent from "eshop/modules/simfree/components/rating/ProductCartProductRecensionComponent";

import {setRating, setMessage,setLogin,sendReview, setShowRatingErrorModal} from "eshop/modules/simfree/actions/app";
import {getRating,getMessage,getLogin, getReviewSend} from "eshop/modules/simfree/selectors";

class ProductCartProductReviewComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this._loadModules();
    }

    showMobileReview() {
        if ($('#opl-rating-form').hasClass("u-small-hide")) {
            $('#opl-rating-form').removeClass("u-small-hide");
        }else{
            $('#opl-rating-form').addClass("u-small-hide");
        }

    }

    handleSendClick(){
        if(this.props.rating > 0 && this.props.login && this.props.message){
            this.props.sendReview(this.props.deviceData.productId, this.props.rating, this.props.login, this.props.message)
        } else {
            this.props.setShowRatingErrorModal(true)
        }
    }
    render() {
        return (
            <div className="js-expander__container opl-wiking-expander__container">
                <h2 id={'product-comments'} data-skiplinks={'Opinie i recenzje'}
                   className="h2 u-padding-l u-small-no-padding-b u-small-padding-left-xl u-no-spacing">Oceny i
                    recenzje</h2><a href="#" className="js-expander__trigger opl-wiking-expander__trigger"><span
                className="g-icon g-icon--only g-icon--arrow-down-slim"></span><span className="u-acc-hide">rozwiń</span></a>
                <div className="js-expander__content opl-wiking-expander__content">
                    <div className="u-small-padding-top-m u-spacing-xl u-small-no-spacing">
                        <div className="opl-wiking-comments">
                            <div className="u-large-hide u-medium-hide">
                                <p className="h1 g-brand2-c u-inline-block u-spacing-right">{this.props.deviceData.averageRatingCustomers}</p>
                                <div className="opl-rating u-vertical-top"><span
                                    className="g-icon g-icon--star g-icon--s"></span><span
                                    className="g-icon g-icon--star g-icon--s"></span><span
                                    className="g-icon g-icon--star g-icon--s"></span><span
                                    className="g-icon g-icon--star g-icon--s"></span><span
                                    className="g-icon g-icon--star g-icon--s"></span>
                                    <div style={{width: this.props.deviceData.percentageRating}} className="opl-rating-bar"><span
                                        className="g-icon g-icon--star g-icon--s"></span><span
                                        className="g-icon g-icon--star g-icon--s"></span><span
                                        className="g-icon g-icon--star g-icon--s"></span><span
                                        className="g-icon g-icon--star g-icon--s"></span><span
                                        className="g-icon g-icon--star g-icon--s"></span></div>
                                </div>
                                <button
                                    className="o-btn opl-btn opl-btn--secondary opl-btn--medium u-spacing-m u-w-100" onClick={(e) => {this.showMobileReview()}}>
                                    Dodaj swoją opinię
                                </button>
                            </div>
                            <div id={'opl-rating-form'} className="l-row u-small-hide">
                                <div className="l-col l-col-small-12 l-col-medium-12 l-col-6 ">
                                    <div className="l-row u-spacing-l">
                                        <div className="l-col l-col-small-12 l-col-medium-3 l-col-4 ">
                                            <div className="opl-rating">
                                                <span className="g-icon g-icon--star g-icon--s"></span><span
                                                className="g-icon g-icon--star g-icon--s"></span><span
                                                className="g-icon g-icon--star g-icon--s"></span><span
                                                className="g-icon g-icon--star g-icon--s"></span><span
                                                className="g-icon g-icon--star g-icon--s"></span>
                                                <div style={{width: this.props.deviceData.percentageRating}}
                                                     className="opl-rating-bar"><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span></div>
                                            </div>
                                        </div>
                                        <div className="l-col l-col-small-12 l-col-medium-9 l-col-8 ">
                                            <h3 className="h4 u-inline-block">Średnia ocena</h3>&nbsp;
                                            <p className="h2 g-brand2-c u-inline-block">{this.props.deviceData.averageRatingCustomers}</p>&nbsp;
                                            <p className="small g-gray4-c u-padding-left-s u-inline-block"
                                               title={'Liczba opini: ' + this.props.deviceData.numberOfReviewsCustomers}>
                                                ({this.props.deviceData.numberOfReviewsCustomers})</p>
                                        </div>
                                    </div>
                                    <ProductCartProductRatingStatisticComponent
                                        ratings={this.props.deviceData.ratingStatistics}/>
                                </div>
                                {this._renderRatingFormSection()}
                            </div>
                            <div className="o-separator u-spacing-xl u-madium-hide u-small-hide"></div>
                            <ProductCartProductRecensionComponent reviews={this.props.deviceData.reviews}/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    _renderRatingFormSection(){
        let content;
        if(this.props.reviewSend){
            content = this._renderRatingSuccessFormSend();
        } else if (this.props.reviewSend === "") {
            content = this._renderRatingForm();
        } else {
            content = this._renderRatingErrorFormSend();
        }
        return (
            <div className="l-col l-col-small-12 l-col-medium-9 l-col-6  medium-offset-by-3">
                {content}
            </div>
        )

    }

    _renderRatingForm() {
        return (
            <div className="l-row u-spacing-xl">
                <div className="l-col l-col-small-12 l-col-medium-10 l-col-12 ">
                    <div className="u-spacing-l">
                        <h3 className="h4 u-inline-block u-padding-right-xl">Dodaj swoją opinię</h3>
                        <fieldset id={'product-cart-opl-rating'} data-js-module={'common/modules/opl-rating'}
                                  className="u-inline-block opl-rating opl-rating--clickable">
                            <label className="o-radio opl-radio">
                                <input type="radio"
                                       name={'rating'}
                                       onClick={this.props.setRating.bind(this, 1)}/><span
                                className="g-icon g-icon--star g-icon--s"></span><span className="o-ci-label u-acc-hide">Ocena 1</span>
                            </label>
                            <label className="o-radio opl-radio">
                                <input type="radio"
                                       name={'rating'}
                                       onClick={this.props.setRating.bind(this, 2)}/><span className="g-icon g-icon--star g-icon--s"></span><span
                                className="o-ci-label u-acc-hide">Ocena 2</span>
                            </label>
                            <label className="o-radio opl-radio">
                                <input type="radio"
                                       name={'rating'}
                                       onClick={this.props.setRating.bind(this, 3)}/><span className="g-icon g-icon--star g-icon--s"></span><span
                                className="o-ci-label u-acc-hide">Ocena 3</span>
                            </label>
                            <label className="o-radio opl-radio">
                                <input type="radio"
                                       name={'rating'}
                                       onClick={this.props.setRating.bind(this, 4)}/><span className="g-icon g-icon--star g-icon--s"></span><span
                                className="o-ci-label u-acc-hide">Ocena 4</span>
                            </label>
                            <label className="o-radio opl-radio">
                                <input type="radio"
                                       name={'rating'}
                                       onClick={this.props.setRating.bind(this, 5)}/><span className="g-icon g-icon--star g-icon--s"></span><span
                                className="o-ci-label u-acc-hide">Ocena 5</span>
                            </label>
                        </fieldset>
                    </div>
                    <input type="text"
                           placeholder={'Nick lub imię'}
                           className="u-spacing opl-input--size-m"
                           onChange={event => {this.props.setLogin(event.target.value)}}/>
                    <textarea className="u-spacing-l"
                              placeholder={'Opinia o produkcie'}
                              onChange={event => {this.props.setMessage(event.target.value)}}></textarea>
                    <div className="l-row u-spacing-xl">
                        <div
                            className="l-col l-col-small-6 l-col-medium-4 l-col-4 medium-offset-by-8 large-offset-by-8">
                            <button className="o-btn opl-btn opl-btn--secondary opl-btn--medium u-w-100"
                                    onClick={() => this.handleSendClick()}>Wyślij
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    _renderRatingSuccessFormSend(){
        return(
            <div className="l-row u-spacing-xl">
                <div className="l-col l-col-small-12 l-col-medium-10 l-col-12 ">
                    <div className="u-spacing-l">
                        <p className="h1 u-inline-block text-center xl">Dziękujemy!!!</p>
                    </div>
                    <div className="u-spacing-l">
                        <p className="h4 u-inline-block text-center xl">Dziękujemy za wyrażenie opinii. Zostanie ona wyświetlona po weryfikacji przez moderatora.</p>
                        <p className="h4 u-inline-block text-center xl">Zapraszamy do ponownego dzielenia się wrażeniami odnośnie użytkowanego sprzętu.</p>
                    </div>
                </div>
            </div>
        )
    }

    _renderRatingErrorFormSend(){
        return(
            <div className="l-row u-spacing-xl">
                <div className="l-col l-col-small-12 l-col-medium-10 l-col-12 ">
                    <div className="u-spacing-l">
                        <p className="h1 u-inline-block text-center xl">Uuups!!!</p>
                    </div>
                    <div className="u-spacing-l">
                        <p className="h4 u-inline-block text-center xl">Wystąpił nieoczekiwany błąd.</p>
                        <p className="h4 u-inline-block text-center xl">Prosimy spróbować ponownie później.</p>
                    </div>
                </div>
            </div>
        )
    }

    _loadModules() {
        OPL.UI.loadModules(document.getElementById("product-cart-opl-rating"), [{
            path: 'common/modules/opl-rating'
        }]);
    }

}

const mapStateToProps = state => ({
    rating: getRating(state),
    message: getMessage(state),
    login: getLogin(state),
    reviewSend: getReviewSend(state)
});

const mapDispatchToProps = (dispatch) => ({
    setRating: (rating) => dispatch(setRating(rating)),
    setLogin: (login) => dispatch(setLogin(login)),
    setMessage: (message) => dispatch(setMessage(message)),
    sendReview: (productCode,rating,login,message) => dispatch(sendReview(productCode,rating,login,message)),
    setShowRatingErrorModal: (showRatingErrorModal) => dispatch(setShowRatingErrorModal(showRatingErrorModal))
});

ProductCartProductReviewComponent.propTypes = {
    deviceData: PropTypes.oneOfType([PropTypes.string,PropTypes.object]).isRequired
};

export default connect(mapStateToProps, mapDispatchToProps)(ProductCartProductReviewComponent)
